body {
	padding: 15px 0;
	overflow: auto;
	background: #eaf7f9;
}
table {
	width: 100%;
	background: #fff;
	margin: 1em 0;
	border: 1px solid rgba(34,36,38,.15);
	&.ui.sheetTable {border: none;}
	box-shadow: none;
	border-radius: .28571429rem;
	text-align: left;
	color: rgba(0,0,0,.87);
	border-collapse: separate;
	border-spacing: 0;

	th {
		cursor: auto;
	    background: #f9fafb;
	    text-align: inherit;
	    color: rgba(0,0,0,.87);
	    padding: .92857143em .78571429em;
	    vertical-align: inherit;
	    font-style: none;
	    font-weight: 700;
	    text-transform: none;
	    border-bottom: 1px solid rgba(34,36,38,.1);
	    border-left: none;
	}
}
#portfolio {
	min-height: 200px;
}

.segment {
	height: inherit;
	&.bottom.attached {
		overflow-y: hidden;
	}
}

#tabMenu {
	margin-top: 40px;
	.item {
		width: 50%;
	    text-align: center;
	    display: inline;
	    &.active {
	    	background: #f9fafb;
	    }
	}

}

.ui.bottom.tab {
	background: #f9fafb;
}

.bottomLink {
	max-width: 100%;
	display: block;
	overflow: auto;
	margin: 10px;
}
#refresh {
	width: 100%;
}
.neg {
color: red;
}
.pos {
color: green;
}
.symbol {
	font-weight: bold;
	color:#15c
}
.bold {
	font-weight: bold;
}